.profile-info_container {
  max-width: 56rem;
  background-color: lightblue;
  margin-inline: auto;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 900px) {
  .profile-info_container {
    flex-direction: row;
    gap: 1rem;
    background-color: lightgreen;
  }
}

.global-container {
  width: 55rem;
  margin-inline: auto;
  max-width: 100%;
}

.card {
  background-color: var(--clr-primary-gray);
  border-radius: 0.4rem;
  margin-bottom: 1rem;
  padding: 1rem;
  color: white;
}
.card > div {
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.card > div > h2 + a {
  color: var(--clr-blue-link);
}
.card a {
  text-decoration: none;
}

.profile-media__button {
  background-color: var(--clr-comment-background);
  border: none;
  padding: 0.7rem;
  border-radius: 0.4rem;
  margin-top: 0.7rem;
  cursor: pointer;
  color: var(--clr-secondary-btn-text);
}
.profile-media__button:hover {
  background-color: var(--clr-secondary-comment-background);
}
.profile-media__button i {
  margin-right: 0.4rem;
}

.active-link {
  color: var(--clr-secondary-blue);
  position: relative;
}
.active-link:hover {
  background-color: none;
}
.active-link::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  bottom: -0.2rem;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--clr-secondary-blue);
}

.header {
  padding: 0.5rem 1rem;
  background-color: var(--clr-primary-gray);
  position: fixed;
  z-index: 1;
  width: 100%;
}
.header .header__navbar {
  display: flex;
  justify-content: space-between;
}
.header .header__navbar div {
  display: flex;
  align-items: center;
  position: relative;
}
.header .header__navbar div .header__search input {
  padding: 0.7rem;
  border: none;
  border-radius: 99px;
  background-color: var(--clr-comment-background);
  padding-left: 2.5rem;
  margin-left: 0.5rem;
  display: none;
}
@media (min-width: 1200px) {
  .header .header__navbar div .header__search input {
    display: block;
  }
}
.header .header__navbar div .header__search svg {
  background-color: var(--clr-comment-background);
  border-radius: 50%;
  box-sizing: content-box;
  color: var(--clr-secondary-icon);
  display: block;
  left: 0;
  margin-left: 0.5rem;
  padding: 0.775rem;
  position: relative;
  top: 50%;
  width: 1rem;
}
@media (min-width: 1200px) {
  .header .header__navbar div .header__search svg {
    position: absolute;
    top: 0;
  }
}
.header .header__navbar ul {
  display: flex;
  justify-content: space-between;
}
.header .header__navbar ul li {
  color: var(--clr-secondary-icon);
}
.header .header__navbar ul li::marker {
  content: "";
}
.header .header__navbar ul img {
  width: 2.5rem;
  border-radius: 50%;
}
.header .header__navbar .header__menu {
  flex-basis: 30rem;
  display: none;
}
.header .header__navbar .header__menu .menu__social-link {
  display: flex;
  justify-content: center;
  flex-basis: 7rem;
  color: var(--clr-secondary-icon);
  border-radius: 7px;
  cursor: pointer;
}
.header .header__navbar .header__menu .menu__social-link:hover {
  background-color: var(--clr-comment-background);
}
.header .header__navbar .header__menu .menu__social-link svg {
  width: 1.5rem;
}
@media (min-width: 700px) {
  .header .header__navbar .header__menu {
    display: flex;
  }
}
.header .header__navbar .header__personal-menu {
  gap: 0.5rem;
}
.header .header__navbar .header__personal-menu .personal-menu__link {
  color: var(--clr-primary-icon);
  height: 1.25rem;
  width: 1.25rem;
  border-radius: 50%;
  box-sizing: content-box;
  background-color: var(--clr-secondary-btn-bg);
  cursor: pointer;
  padding: 0.7rem;
}
.header .header__navbar .header__personal-menu .personal-menu__link:last-child {
  width: 2.5rem;
  height: 2.5rem;
  padding: 0;
  position: relative;
}
.header .header__navbar .header__personal-menu .personal-menu__link:last-child::before {
  position: absolute;
  content: url(../../images/header/dropdown-account.svg);
  width: 1rem;
  height: 1rem;
  background-color: var(--clr-secondary-comment-background);
  bottom: -5px;
  right: -5px;
  border-radius: 50%;
  border: solid 3px var(--clr-primary-gray);
}
.header .header__navbar .header__personal-menu .personal-menu__link:hover {
  background-color: var(--clr-secondary-comment-background);
}
.header .header__navbar .header__personal-menu .personal-menu__link svg {
  width: 1.25rem;
}
.header .header__navbar .header__personal-menu .personal-menu__link:first-child svg:last-child {
  display: block;
}
@media (min-width: 1100px) {
  .header .header__navbar .header__personal-menu .personal-menu__link:first-child svg:last-child {
    display: none;
  }
}
.header .header__navbar .header__personal-menu .personal-menu__link:first-child svg:first-child {
  display: none;
}
@media (min-width: 1100px) {
  .header .header__navbar .header__personal-menu .personal-menu__link:first-child svg:first-child {
    display: block;
  }
}

.banner .banner__coverphoto {
  background-image: linear-gradient(#fdf0ce, var(--clr-primary-gray));
}
.banner .banner__coverphoto .image-container {
  max-width: 60rem;
  margin-inline: auto;
  min-height: 23rem;
  overflow: hidden;
  position: relative;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}
.banner .banner__coverphoto .image-container img {
  min-width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.banner .banner__coverphoto .image-container .banner__coverphoto--edit {
  background-color: #fff;
  border-radius: 6px;
  bottom: 1rem;
  box-shadow: 0 0 5px gray;
  display: flex;
  gap: 5px;
  padding: 8px 10px;
  position: absolute;
  right: 2rem;
}
@media (max-width: 900px) {
  .banner .banner__coverphoto .image-container .banner__coverphoto--edit p {
    display: none;
  }
}
.banner .banner__edit {
  background-color: var(--clr-primary-gray);
}
.banner .banner__edit .global-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 900px) {
  .banner .banner__edit .global-container {
    flex-direction: row;
  }
}
.banner .banner__edit .banner__display {
  display: flex;
  gap: 1.2rem;
}
@media (max-width: 900px) {
  .banner .banner__edit .banner__display {
    flex-direction: column;
    align-items: center;
    position: relative;
    top: -1rem;
    gap: 0;
  }
}
.banner .banner__edit .banner__display .banner__personal-img {
  width: 11rem;
  height: 11rem;
  position: relative;
  bottom: 1.5rem;
  cursor: pointer;
}
.banner .banner__edit .banner__display .banner__personal-img img {
  width: 100%;
  border-radius: 50%;
  border: solid 4px var(--clr-primary-gray);
}
.banner .banner__edit .banner__display .banner__personal-img::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  border-radius: 50%;
}
.banner .banner__edit .banner__display .banner__personal-img:hover::before {
  background-color: rgba(255, 255, 255, 0.096);
}
.banner .banner__edit .banner__display .banner__personal-img i {
  bottom: 0.5rem;
  color: var(--clr-primary-icon);
  position: absolute;
  right: 0.5rem;
  font-size: 1.3rem;
  background-color: var(--clr-secondary-comment-background);
  padding: 0.5rem;
  border-radius: 50%;
}
.banner .banner__edit .banner__display .banner__info {
  padding-top: 1.3rem;
}
@media (max-width: 900px) {
  .banner .banner__edit .banner__display .banner__info {
    text-align: center;
    padding-top: 0;
    position: relative;
    top: -1rem;
  }
}
.banner .banner__edit .banner__display .banner__info h1 {
  color: white;
  font-size: 2rem;
}
.banner .banner__edit .banner__display .banner__info a {
  color: var(--clr-secondary-text);
  text-decoration: none;
}
.banner .banner__edit .banner__display .banner__info a:hover {
  text-decoration: underline;
}
.banner .banner__edit .banner__display .banner__info .friends-list {
  display: flex;
  margin-top: 0.7rem;
}
.banner .banner__edit .banner__display .banner__info .friends-list div {
  width: 2.5rem;
}
.banner .banner__edit .banner__display .banner__info .friends-list div img {
  width: 100%;
}
.banner .banner__edit .banner__media {
  display: flex;
  align-items: center;
  color: white;
  margin-top: 3.5rem;
}
@media (max-width: 900px) {
  .banner .banner__edit .banner__media {
    position: relative;
    top: -1rem;
    margin-top: 0;
  }
}
.banner .banner__edit .banner__media div {
  display: flex;
  background-color: var(--clr-comment-background);
  margin-right: 1rem;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.banner .banner__edit .banner__media div img {
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
  filter: invert(89%) sepia(6%) hue-rotate(185deg);
}
.banner .banner__edit .banner__media div:first-child {
  background-color: var(--clr-primary-blue);
}
.banner .banner__edit .banner__media i {
  background-color: var(--clr-comment-background);
  border-radius: 5px;
  padding: 13.6px 17px;
  font-size: 0.7rem;
  cursor: pointer;
}
.banner .banner__profile-links {
  background-color: var(--clr-primary-gray);
}
@media (max-width: 900px) {
  .banner .banner__profile-links {
    position: relative;
    padding-inline: 1rem;
  }
}
.banner .banner__profile-links .global-container {
  display: flex;
  justify-content: space-between;
  border-top: solid 1px gray;
  padding: 0.2rem 0;
  align-items: center;
}
.banner .banner__profile-links .global-container > i {
  background-color: var(--clr-comment-background);
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.7rem;
  padding: 13.6px 20px;
  color: white;
}
.banner .banner__profile-links .links {
  display: flex;
}
.banner .banner__profile-links .links .profile-link a {
  color: var(--clr-secondary-text);
  text-decoration: none;
  display: block;
  padding: 1rem;
  border-radius: 0.4rem;
}
.banner .banner__profile-links .links .profile-link a:hover {
  background-color: var(--clr-comment-background);
}
.banner .banner__profile-links .links .profile-link::marker {
  content: "";
}
.banner .banner__profile-links .links .profile-link :last-child i {
  margin-left: 0.5rem;
}

.profile-media {
  background-color: var(--clr-primary-gray-dark);
}
.profile-media .global-container {
  display: flex;
  gap: 1rem;
  padding: 1rem 0;
}
@media (max-width: 900px) {
  .profile-media .global-container {
    flex-direction: column;
    padding: 1rem;
  }
}
.profile-media .global-container .left {
  width: 43%;
}
.profile-media .global-container .left > p,
.profile-media .global-container .left > p a {
  color: var(--clr-secondary-text);
  font-size: 0.8rem;
}
.profile-media .global-container .left > p a {
  text-decoration: none;
}
.profile-media .global-container .left > p a:hover {
  text-decoration: underline;
}
.profile-media .global-container .left .intro .intro__sec {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.profile-media .global-container .left .intro .intro__details .details li {
  display: flex;
  align-items: start;
  text-align: start;
}
.profile-media .global-container .left .intro .intro__details .details li::marker {
  content: "";
}
.profile-media .global-container .left .intro .intro__details .details li img {
  filter: invert(89%) sepia(6%) hue-rotate(185deg);
  margin-right: 0.5rem;
}
.profile-media .global-container .left .intro .intro__details .details li a {
  color: white;
  text-decoration: none;
}
.profile-media .global-container .left .intro .intro__details .details li a:hover {
  text-decoration: underline;
}
.profile-media .global-container .left .intro .intro__details .details li:not(:last-child) {
  margin: 1.3rem 0;
}
@media (max-width: 900px) {
  .profile-media .global-container .left {
    width: 100%;
    max-width: 40rem;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }
}
.profile-media .global-container .right {
  width: 57%;
}
.profile-media .global-container .right .whats-in-your-mind div:first-child {
  border-bottom: solid 1px var(--clr-secondary-btn-bg);
  padding-bottom: 0.7rem;
}
.profile-media .global-container .right .whats-in-your-mind div:first-child img {
  border-radius: 50%;
}
.profile-media .global-container .right .whats-in-your-mind div:first-child input {
  width: 100%;
  border: none;
  border-radius: 99px;
  background-color: var(--clr-comment-background);
  outline: none;
  padding: 10px;
  margin-left: 0.5rem;
  font-size: 1.1rem;
  cursor: pointer;
  caret-color: white;
}
.profile-media .global-container .right .whats-in-your-mind div:first-child input:hover {
  background-color: var(--clr-secondary-comment-background);
}
.profile-media .global-container .right .whats-in-your-mind div:last-child {
  justify-content: center;
  margin-top: 0.5rem;
}
.profile-media .global-container .right .whats-in-your-mind div:last-child p {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 31%;
  gap: 0.5rem;
  padding: 0.5rem 0;
  border-radius: 0.4rem;
  cursor: pointer;
}
.profile-media .global-container .right .whats-in-your-mind div:last-child p:hover {
  background-color: var(--clr-comment-background);
}
.profile-media .global-container .right .whats-in-your-mind div:last-child p span {
  font-size: 0.9rem;
  color: var(--clr-secondary-text);
  font-weight: bold;
}
.profile-media .global-container .right .posts-management {
  padding-inline: 0;
  padding-bottom: 0;
}
.profile-media .global-container .right .posts-management .posts-management__buttons {
  margin-top: 0.2rem;
}
.profile-media .global-container .right .posts-management .posts-management__buttons a:first-child {
  margin-right: 0.2rem;
}
.profile-media .global-container .right .posts-management > div:first-child {
  padding-inline: 1rem;
}
